<template>
    <div class="form-schedule" id="form-schedule">
        <table class="hidden-xs">
            <thead>
                <tr>
                    <th>Рівень</th>
                    <th>Стартує</th>
                    <th>Час та дні</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in groups" :key="index">
                    <td><div><span><i>{{ item.level_code }}</i></span><p>{{ item.level_title }}</p></div></td>
                    <td><span>{{ new Date(item.start_date).toLocaleDateString()}}</span></td>
                    <td><p><span>{{ item.time }}</span>
                        <template v-for="(day, key) in item.days">
                            <span :key="key">
                                 {{ daysList[day].short }}<template v-if="key < item.days.length - 1">, </template>
                            </span>

                        </template>
                    </p></td>
                    <td>
                        <div class="btn-box">
                            <div class="btn btn-1" @click="openModal()">
                                Дізнайся свій рівень
                            </div>
                        </div>
                    </td>
                </tr>
                <tr><td colspan="5" class="--white"></td></tr>
            </tbody>
            <tfoot>
            </tfoot>
        </table>
        <div class="schedule-item-container">
            <div class="schedule-item">
                <div class="schedule-item-head"></div>
                <div class="schedule-item-body"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        getGroup
    } from '../../api';
    import { mapActions } from 'vuex';
    import { daysList } from "../../const/daysList";

    export default {
        components: {},
        data() {
            return {
                data: {
                    selected: 'underwater',
                    group: null
                },
                groups: null,
                daysList
            };
        },
        methods: {
            ...mapActions('modals', ['openBaseModal']),
            changeCheckbox(index) {
                this.data.selected = index;
                let result = this.groups.filter(obj => {
                    return obj['course'] === index && ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                })[0];
                this.data.start_date = result.start_date;
                this.data.price = result.price;
                this.data.discount_price = result.discount_price;
                this.data.id = result.id;
                this.data.group = result;

            },
            openModal() {
                this.openBaseModal(this.data);
            },
        },
        created() {
            const params = {
                course: 'lifestyle',
                type: 'adults'
            };
            getGroup(params).then(
                result => {
                    this.groups = result.map(group => {
                        return {
                            ...group,
                            days: JSON.parse(group.days)
                        }
                    });
                },
                error => {
                    console.warn(error)
                }
            );
        },
        mounted() {
        }
    }
</script>
